:root {
  --color-dim: #ddd;
  --color-light: #eee;
  --color-font: #000;
  --color-accent: #e80265;
  --color-placeholder: #666;
  --corner-radius: .4rem;
  --transition-slow: .6s;
  --transition-med: .2s;
  --transition-fast: .1s;
}

.reflex-single-container > * {
  width: 100%;
  height: 100%;
}

body {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
}

.reflex-widget {
  box-sizing: border-box;
}

.reflex-align > *, .reflex-margin > * {
  position: relative;
}

.reflex-column {
  flex-direction: column;
  align-items: stretch;
  display: flex;
}

.reflex-row {
  flex-direction: row;
  align-items: stretch;
  display: flex;
}

.reflex-text {
  justify-content: center;
  align-items: center;
  display: flex;
}

.reflex-rectangle {
  background: var(--rectangle-background);
  border-style: solid;
  border-color: var(--rectangle-stroke-color);
  border-width: var(--rectangle-stroke-width);
  border-radius: var(--rectangle-corner-radius-top-left) var(--rectangle-corner-radius-top-right) var(--rectangle-corner-radius-bottom-right) var(--rectangle-corner-radius-bottom-left);
  box-shadow: var(--rectangle-shadow-offset-x) var(--rectangle-shadow-offset-y) var(--rectangle-shadow-radius) var(--rectangle-shadow-color);
  transition-timing-function: ease;
}

.reflex-rectangle-hover:hover {
  background: var(--rectangle-background-hover);
  border-color: var(--rectangle-stroke-color-hover);
  border-width: var(--rectangle-stroke-width-hover);
  border-radius: var(--rectangle-corner-radius-top-left-hover) var(--rectangle-corner-radius-top-right-hover) var(--rectangle-corner-radius-bottom-right-hover) var(--rectangle-corner-radius-bottom-left-hover);
  box-shadow: var(--rectangle-shadow-offset-x-hover) var(--rectangle-shadow-offset-y-hover) var(--rectangle-shadow-radius-hover) var(--rectangle-shadow-color-hover);
}

.reflex-stack > * {
  position: absolute;
  inset: 0;
}

.reflex-text-input {
  background-color: var(--color-dim);
  color: #000;
  caret-color: var(--color-accent);
  border-radius: var(--corner-radius);
  text-align: center;
  transition: background-color var(--transition-slow) ease-in-out;
  -moz-transition: background-color var(--transition-slow) ease-in-out;
  transition: border var(--transition-fast) ease-in-out;
  -moz-transition: border var(--transition-fast) ease-in-out;
  border: 1.5px solid #0000;
  outline: none;
  min-height: 1.2rem;
  padding: .3rem .5rem;
}

.reflex-text-input:hover, .reflex-text-input:focus {
  background-color: var(--color-light);
  border: 1.5px solid var(--color-accent);
}

::placeholder {
  color: var(--color-placeholder);
}

.reflex-dropdown {
  background-color: var(--color-dim);
  color: #000;
  caret-color: var(--color-accent);
  border-radius: var(--corner-radius);
  text-align: center;
  border: 1.5px solid #0000;
  outline: none;
  min-height: 2rem;
  padding: .3rem .5rem;
}

.reflex-dropdown:hover {
  background-color: var(--color-light);
  border: 1.5px solid var(--color-accent);
}

.reflex-progress-circle {
  width: 0;
  height: 0;
}

.reflex-progress-circle circle {
  fill: none;
  stroke-width: 3.5px;
}

.spinning svg {
  transform-origin: center;
  animation: 2s linear infinite rotate;
}

.spinning .progress {
  stroke-dasharray: 1 200;
  stroke-dashoffset: 0;
  stroke-linecap: round;
  animation: 1.5s ease-in-out infinite dash;
}

.spinning .background {
  stroke: none;
}

.reflex-progress-circle:not(.spinning) .progress {
  stroke-dashoffset: -94.25px;
  stroke-dasharray: var(--dasharray);
  transition: stroke-dasharray .5s;
}

.reflex-progress-circle:not(.spinning) .background {
  stroke: var(--background-color);
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1 200;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 90 200;
    stroke-dashoffset: -35px;
  }

  100% {
    stroke-dashoffset: -125px;
  }
}

.reflex-switch .container {
  background-color: var(--switch-background-color-off);
  z-index: 1;
  border-radius: 4.3em;
  width: 3.182em;
  height: 1.548em;
  margin: auto;
  transition: all .3s;
  position: relative;
}

.reflex-switch.is-on .container {
  background-color: var(--switch-background-color-on);
}

.reflex-switch input {
  opacity: 0;
  cursor: pointer;
  z-index: 3;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  position: relative;
}

.reflex-switch .knob {
  z-index: 2;
  background-color: var(--switch-knob-color-off);
  border-radius: 50%;
  width: .86em;
  height: .43em;
  padding: .387em .172em;
  transition: all .3s, left .3s cubic-bezier(.18, .89, .35, 1.15);
  position: absolute;
  top: .172em;
  left: .172em;
}

.reflex-switch input:active + .knob {
  border-radius: 4.3em;
  width: 1.978em;
}

.reflex-switch.is-on input:active + .knob {
  margin-left: -1.118em;
}

.reflex-switch.is-on input + .knob {
  background-color: var(--switch-knob-color-on);
  left: 1.806em;
}

